.contact{
    width:100%;
    text-align: left;
    display: flex;
    align-items: flex-end;
    justify-content: right;
    padding-right:2.4vw;
    padding-bottom:2.4vw;
}

h2{
    color: white;
    font-weight: 800;
    font-size: 1em;
    margin: 0;
}

h5{
    padding: 0;
    color: white;
    font-weight: 400;
    font-size: .8em;
    margin: 0;

}

h5:hover{
    color: lightgreen;
    text-decoration: underline;
    cursor: pointer;
}
.contact-header{
    font-size: 1.6em;
    font-weight: 800;
    color: white;
    text-transform:uppercase;
    margin: 0;
    margin-bottom: .5em;
}

.contact-card{
    display: flex;
    flex-direction: column;
    background-color: black;
    width: 20vw;
    height: 36vh;
    padding:1.6em;
}

.contact-box{
    color: white;
    display: flex;
    flex-direction: column;
    background-color: black;
    width: 20vw;
    height: 36vh;
    padding:1.6em;
}